            dialog4:false,
            id1:0,
            dialog5:false
        }
    },mixins:[loadMore],methods: {
        loadMores(t)
        {
            if(t>1300)
            {
                this.dialog5=true;
            }else{
                this.dialog5=false;
            }
        },
        show()
        {
            this.dialog=!this.dialog;
        },
        show1()
        {
            this.dialog1=!this.dialog1;
        },
        show2()
        {
           
